import React from 'react';
import { cache } from '@cornerstonejs/core';
import PropTypes from 'prop-types';
import { ViewportImplantForm } from '@ohif/ui';
import {
  segmentation as cstSegmentation,
  Types as cstTypes,
  Enums as cstEnums,
} from '@cornerstonejs/tools';
import { getAxis, findStart, fillCylinder, fillSTL } from './implantUtils';
import implantT3ST410 from './implants/T3ST410.json';

import vtkDataArray from '@kitware/vtk.js/Common/Core/DataArray';
import vtkImageData from '@kitware/vtk.js/Common/DataModel/ImageData';
import { Dentascan } from '../types/Panoramic';

const CornerstoneViewportImplantForm = ({ onClose, servicesManager }) => {
  const displayImplantIn3DViewport = async (
    implantMetadata,
    toothNumber: number,
    imageData: vtkImageData,
    voxelManager
  ) => {
    const { cornerstoneCacheService, segmentationService } = servicesManager.services;
    const { data: implantData, dims: implantDimensions, start } = implantMetadata;

    const implantImageData = vtkImageData.newInstance();
    implantImageData.setDimensions(implantDimensions);
    const origin = imageData.indexToWorld(start);
    implantImageData.setOrigin([origin[0], origin[1], origin[2]]);
    implantImageData.setDirection(imageData.getDirection());
    implantImageData.setSpacing(imageData.getSpacing());
    const implantDataArray = vtkDataArray.newInstance({
      numberOfComponents: voxelManager.numberOfComponents,
      values: implantData,
      name: '',
    });
    implantImageData.getPointData().setScalars(implantDataArray);

    const surfaceActorEntries = cornerstoneCacheService.getSurfaceActorEntries();
    if (surfaceActorEntries.length) {
      // Update surfaceActorEntries
      const segmentation: cstTypes.Segmentation = segmentationService.getGeneratedSegmentation();
      const { segmentIndex: implantIdx } = Object.values(segmentation.segments).find(
        segment => segment?.label === 'Implant'
      );
      const label = `Implant_${toothNumber}`;
      segmentationService
        .createSurfacesData(segmentation, origin, [implantIdx], label)
        .then(actorEntries =>
          cornerstoneCacheService.setSurfaceActorEntries([...surfaceActorEntries, actorEntries[0]])
        );
    }
  };

  const displayImplantInPanoramic = async (
    segmentation: cstTypes.Segmentation,
    dentascans: Dentascan[]
  ) => {
    const { displaySetService, segmentationService, viewportGridService } =
      servicesManager.services;

    const implantSegment = Object.values(segmentation.segments).find(
      segment => segment?.label === 'Implant'
    );
    const implantSegIdx = implantSegment.segmentIndex;
    const panoramicSegId = 'panoramic_implant';
    let panoramicSegmentation = segmentationService.getSegmentation(panoramicSegId);
    if (!panoramicSegmentation) {
      // Create new panoramic segmentation
      const panoramicDisplaySetUID =
        viewportGridService.getDisplaySetsUIDsForViewport('panoramic')[0];
      const panoramicDisplaySet = displaySetService.getDisplaySetByUID(panoramicDisplaySetUID);
      const panoramicSegments = { [implantSegIdx]: implantSegment };
      const options = {
        segmentationId: panoramicSegId,
        segments: panoramicSegments,
        label: 'Panoramic implants',
      };
      await segmentationService.createLabelmapForDisplaySet(panoramicDisplaySet, options);

      const colorLUTIndex = segmentationService._segmentationIdToColorLUTIndexMap.get(
        segmentation.segmentationId
      );
      segmentationService._segmentationIdToColorLUTIndexMap.set(panoramicSegId, colorLUTIndex);
      await segmentationService.addSegmentationRepresentation('panoramic', {
        segmentationId: panoramicSegId,
        type: cstEnums.SegmentationRepresentations.Labelmap,
      });
      segmentationService.setActiveSegmentation('panoramic', panoramicSegId);
    }

    const imageIds = cstSegmentation.getLabelmapImageIds(panoramicSegId);
    const panoramicSegImage = cache.getImage(imageIds[0]);
    const panoramicSegPixelData = panoramicSegImage.getPixelData();
    const dentascanSegmentation = segmentationService.getGeneratedDentascanSegmentation();
    const { voxelManager: dentascanSegVoxelManager } = segmentationService.getLabelmapVolume(
      dentascanSegmentation.segmentationId
    );
    const dentascanSegScalarData = dentascanSegVoxelManager.getCompleteScalarDataArray();
    const panoramicWidth = dentascans.length;
    const [dentascanWidth, dentascanHeight] = dentascans[0].shape;
    for (let i = 0; i < panoramicWidth; i++) {
      for (let layer = 0; layer < dentascanHeight; layer++) {
        const start = i * dentascanWidth * dentascanHeight + layer * dentascanWidth;
        const dentascanRow = dentascanSegScalarData.slice(start, start + dentascanWidth);
        if (dentascanRow.some(value => value === implantSegIdx)) {
          panoramicSegPixelData[layer * panoramicWidth + (panoramicWidth - i - 1)] = implantSegIdx;
        }
      }
    }
    cstSegmentation.triggerSegmentationEvents.triggerSegmentationDataModified(panoramicSegId);
  };

  const addImplant = async (toothNumber, implant) => {
    const { viewportGridService, segmentationService, panoramicService } = servicesManager.services;

    const segmentation = segmentationService.getGeneratedSegmentation();
    const { voxelManager, dimensions, spacing, imageData } = segmentationService.getLabelmapVolume(
      segmentation.segmentationId
    );

    const displaySetUID = viewportGridService.getDisplaySetsUIDsForViewport('default')[0];
    const panoramicState = panoramicService.getPanoramicState(displaySetUID);
    const { teethAxes, minMaxTissueLayer, dentascans } = panoramicState;

    const axis = getAxis(segmentation.segments, teethAxes, toothNumber); // [vz, vy, vx, z0, y0, x0]
    if (!axis) {
      return false;
    }
    const tStart = findStart(voxelManager, dimensions, toothNumber, axis, minMaxTissueLayer);
    if (tStart === undefined) {
      return false;
    }

    if (!implant) {
      fillCylinder(voxelManager, dimensions, toothNumber, axis, tStart, spacing);
    } else {
      const implantMetadata = fillSTL(voxelManager, toothNumber, axis, tStart, spacing, implant);
      displayImplantIn3DViewport(implantMetadata, toothNumber, imageData, voxelManager);
    }

    panoramicService.copySegmentationVolumesForDentascan();
    cstSegmentation.triggerSegmentationEvents.triggerSegmentationDataModified(
      segmentation.segmentationId
    );
    displayImplantInPanoramic(segmentation, dentascans);

    onClose();
    return true;
  };

  return (
    <ViewportImplantForm
      onClose={onClose}
      addImplant={(toothNumber: number) => addImplant(toothNumber, implantT3ST410)}
    />
  );
};

CornerstoneViewportImplantForm.propTypes = {
  onClose: PropTypes.func.isRequired,
  servicesManager: PropTypes.object.isRequired,
};

export default CornerstoneViewportImplantForm;
